.main-wrapper {
  background-color: #fff;
  position: absolute;
  height: 100vh;
  width: 50px;
  z-index: 100;

  .menu-button-wrapper {
    background-color: #fff;
    position: absolute;
    top: 50px;
    left: 50px;
    height: 150px;
    width: 100px;
    padding: 20px 10px;
    box-sizing: border-box;

    .logo-wrapper {}

    .btn-menu {
      margin-top: 20px;

      img {
        width: 50px;
      }
    }
  }

  .title {
    transform: rotate(270deg);
    transform-origin: 0 0;
    text-align: center;
    margin: 0 auto;
    box-sizing: border-box;
    position: absolute;
    top: 100vh;
    width: 100vh;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .title-inner-wrapper {
      letter-spacing: 17px;
      padding: 0 0 0 17px;
      border-left: 5px solid #F04922;
      border-right: 5px solid #F04922;
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 640px) {
  .main-wrapper {
    position: absolute;
    width: 100%;
    height: unset;
    top: 0;
    left: 0;

    .menu-button-wrapper {
      position: relative;
      top: unset;
      left: unset;
      width: 100%;
      height: 50px;
      padding: 20px 10px 10px 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;

      .logo-wrapper {
        height: 100%;
      }

      .btn-menu {
        height: 100%;
        margin-top: 0;

        img {}
      }
    }

    .title {
      transform: unset;
      width: 60%;
      height: unset;
      top: calc(60px - 22px - 10px);
      left: 0;
      right: 0;

      .title-inner-wrapper {
        font-size: 16px;
        letter-spacing: 5px;
        padding: 0 0 0 5px;
      }
    }
  }
}