.main-wrapper {
  background-image: url("../../assets/pc_game2_bg.jpg");
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: top center;

  .title-wrapper {
    height: 20%;
    display: flex;
    justify-content: flex-end;
    padding: 20px;
  }

  .chatbox-wrapper {
    position: absolute;
    height: 65%;
    left: 60%;

    .chatbox-inner-wrapper {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 120px 170px 10px 70px;
      box-sizing: border-box;
      color: #ffffff;
      letter-spacing: 8px;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;

      .message-wrapper {
        width: 100%;

        .message {
          line-height: 1.2em;
          font-size: 22px;
        }

        .desc {
          font-size: 16px;
          margin-top: 10px;
        }

        span {
          color: #F04922;
        }
      }

      .buttons-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        flex: 1 0 120%;

        .button-wrapper {
          background-repeat: no-repeat;
          width: 80px;
          height: 80px;
          background-size: contain;

          &.btn-book {
            background-image: url("../../assets/btn-book.svg");
          }

          &.btn-workshop {
            background-image: url("../../assets/btn-workshop.svg");
          }

          &.btn-web {
            background-image: url("../../assets/btn-web.svg");
          }

          &.btn-video {
            background-image: url("../../assets/btn-video.svg");
          }

          &.btn-restart {
            background-image: url("../../assets/btn-restart.svg");
          }
        }

      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .main-wrapper {
    

    .title-wrapper {
    }

    .chatbox-wrapper {

      .chatbox-inner-wrapper {

        .message-wrapper {

          .message {
          }

          .desc {
          }

          span {}
        }


        .buttons-wrapper {

          .button-wrapper {

            &.btn-book {}

            &.btn-workshop {}

            &.btn-web {}

            &.btn-video {}

            &.btn-restart {}
          }

        }
      }
    }
  }
}

@media screen and (max-width: 640px) {
  .main-wrapper {
    background-image: url("../../assets/m_game2_bg.jpg");
    height: calc(100vh - 60px);
    margin-top: 60px;
    overflow: hidden;
    position: relative;

    .title-wrapper {
      height: 10%;
    }

    .chatbox-wrapper {
      height: unset;
      width: 100%;
      right: -30%;
      top: 30%;
      left: unset;

      .chatbox-inner-wrapper {
        padding: 70px 30% 10px 30px;
        letter-spacing: 4px;

        .message-wrapper {

          .message {
            font-size: 14px;
            line-height: 1em;
          }

          .desc {
            font-size: 14px;
          }

          span {}
        }


        .buttons-wrapper {
          flex-wrap: wrap;
          justify-content: center;
          flex: 1 0 120%;

          .button-wrapper {
            width: 60px;
            height: 60px;
            margin: 0 10px;

            &.btn-book {}

            &.btn-workshop {}

            &.btn-web {}

            &.btn-video {}

            &.btn-restart {}
          }

        }
      }
    }
  }
}